* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background: #151e3d;
}
#movies-wrapper {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  padding: 5% 5%;
}
.movies-poster {
  width: 22%;
  margin-right: 30px;
  margin-bottom: 30px;
  position: relative;
  overflow: hidden;
}
.movies-poster img {
  width: 100%;
  height: 100%;
}
form {
  margin-top: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
#search-box {
  width: 70%;
  height: 30px;
  border-radius: 20px;
  border: none;
  text-align: center;
  background-color: #485eb1;
  outline: none;
}
#sub-btn {
  position: absolute;
  right: 16%;
  border: none;
  background-color: transparent;
}
#movie-title {
  position: absolute;
  bottom: 0;
  color: white;
  background-color: #151e3dc2;
  width: 100%;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  transform: translateY(100%);
  transition: transform 0.5s ease-in-out;
}
.movies-poster:hover #movie-title {
  transform: translateY(0%);
}
